import { useState, useEffect } from 'preact/hooks';
import { route } from 'preact-router';
import axios from 'axios';
import { formatDistanceToNow } from 'date-fns';
import { useAuth } from '../contexts/AuthContext';
import { getApiUrl } from '../config';

const Home = () => {
  const { user } = useAuth();
  const [people, setPeople] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [searchField, setSearchField] = useState('firstName');
  const [timeFilter, setTimeFilter] = useState('all');
  const [filteredPeople, setFilteredPeople] = useState([]);
  const [showFilters, setShowFilters] = useState(false);
  const [showAddModal, setShowAddModal] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState(null);
  const [showAddressField, setShowAddressField] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [newPerson, setNewPerson] = useState({
    firstName: '',
    lastName: '',
    phoneNumber: '',
    lastContactedDate: new Date().toISOString(),
    address: {
      street: '',
      city: '',
      state: '',
      zipCode: ''
    }
  });

  useEffect(() => {
    if (!user) {
      route('/login');
      return;
    }
    fetchPeople();
  }, [user]);

  const fetchPeople = async () => {
    setIsLoading(true);
    try {
      const response = await axios.get(getApiUrl('/people'));
      setPeople(response.data);
    } catch (error) {
      console.error('Error fetching people:', error);
      if (error.response?.status === 401) {
        route('/login');
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleAddPerson = async (e) => {
    e.preventDefault();
    setError(null);
    
    // Validate required fields
    if (!newPerson.firstName.trim()) {
      setError('First name is required');
      return;
    }
    if (!newPerson.lastName.trim()) {
      setError('Last name is required');
      return;
    }
    if (!newPerson.phoneNumber.trim()) {
      setError('Phone number is required');
      return;
    }
    
    try {
      setIsSubmitting(true);
      const response = await axios.post(getApiUrl('/people'), newPerson);
      const newPersonData = response.data;
      
      // Update the local state
      setPeople(prevPeople => [...prevPeople, newPersonData]);
      
      // Reset form and close modal
      setNewPerson({
        firstName: '',
        lastName: '',
        phoneNumber: '',
        lastContactedDate: new Date().toISOString(),
        address: {
          street: '',
          city: '',
          state: '',
          zipCode: ''
        }
      });
      setShowAddModal(false);
      
      // Navigate to the new person's detail page
      route(`/person/${newPersonData.id}`);
    } catch (error) {
      console.error('Error adding person:', error);
      setError(error.response?.data?.detail || 'Failed to add person');
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewPerson(prev => ({
      ...prev,
      [name]: value
    }));
  };

  useEffect(() => {
    // Filter and search logic
    let filtered = [...people];

    // Apply search
    if (searchTerm) {
      filtered = filtered.filter(person => {
        const searchValue = searchTerm.toLowerCase();
        if (searchField === 'firstName') {
          return person.firstName.toLowerCase().includes(searchValue);
        } else if (searchField === 'lastName') {
          return person.lastName.toLowerCase().includes(searchValue);
        }
        return true;
      });
    }

    // Apply time filter
    if (timeFilter !== 'all') {
      const now = new Date();
      filtered = filtered.filter(person => {
        const lastContactDate = new Date(person.lastContactedDate);
        const diffInDays = (now - lastContactDate) / (1000 * 60 * 60 * 24);
        
        switch (timeFilter) {
          case 'week': return diffInDays <= 7;
          case 'month': return diffInDays <= 30;
          case 'threeMonths': return diffInDays <= 90;
          default: return true;
        }
      });
    }

    setFilteredPeople(filtered);
  }, [people, searchTerm, searchField, timeFilter]);

  const getCardColor = (lastContactedDate) => {
    const daysSinceContact = (new Date() - new Date(lastContactedDate)) / (1000 * 60 * 60 * 24);
    
    // Start with blue for recent contacts, transition to red for older ones
    const urgencyFactor = Math.min(1, daysSinceContact / 90); // Max out at 90 days
    
    // RGB values for the transition from blue to red
    const red = Math.round(59 + (196 * urgencyFactor)); // 59 -> 255
    const green = Math.round(130 - (130 * urgencyFactor)); // 130 -> 0
    const blue = Math.round(246 - (246 * urgencyFactor)); // 246 -> 0
    
    return `rgba(${red}, ${green}, ${blue}, ${Math.max(0.3, 1 - (daysSinceContact / 180))})`; // Fade opacity over 180 days
  };

  return (
    <div class="space-y-6">
      {/* Sticky Search and Filter Controls */}
      <div class="sticky top-[4.5rem] z-10 bg-white/95 backdrop-blur-md rounded-xl border border-gray-200 shadow-sm">
        <div class="p-4 sm:p-6">
          <div class="flex flex-col sm:flex-row gap-4">
            <div class="relative flex-grow">
              <div class="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                <svg class="h-5 w-5 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                </svg>
              </div>
              <input
                type="text"
                placeholder="Search people..."
                value={searchTerm}
                onInput={(e) => setSearchTerm(e.target.value)}
                class="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
              />
            </div>
            <div class="flex gap-2">
              <button
                onClick={() => setShowFilters(!showFilters)}
                class="px-4 py-2 bg-indigo-500 text-white rounded-lg hover:bg-indigo-600 transition-colors flex items-center gap-2 whitespace-nowrap"
              >
                <svg class="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M3 4a1 1 0 011-1h16a1 1 0 011 1v2.586a1 1 0 01-.293.707l-6.414 6.414a1 1 0 00-.293.707V17l-4 4v-6.586a1 1 0 00-.293-.707L3.293 7.293A1 1 0 013 6.586V4z" />
                </svg>
                Filters
                {(searchField !== 'firstName' || timeFilter !== 'all') && (
                  <span class="flex h-2 w-2 relative">
                    <span class="animate-ping absolute inline-flex h-full w-full rounded-full bg-white opacity-75"></span>
                    <span class="relative inline-flex rounded-full h-2 w-2 bg-white"></span>
                  </span>
                )}
              </button>
              <button
                onClick={() => setShowAddModal(true)}
                class="px-4 py-2 bg-green-500 text-white rounded-lg hover:bg-green-600 transition-colors flex items-center gap-2 whitespace-nowrap"
              >
                <svg class="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
                </svg>
                Add Person
              </button>
            </div>
          </div>

          {showFilters && (
            <div class="mt-4 pt-4 border-t border-gray-200">
              <div class="grid grid-cols-1 lg:grid-cols-2 gap-6">
                <div class="space-y-2">
                  <label class="block text-sm font-medium text-gray-700">Search by</label>
                  <div class="flex gap-2">
                    <button
                      onClick={() => setSearchField('firstName')}
                      class={`flex-1 px-4 py-2 rounded-lg border transition-colors ${
                        searchField === 'firstName'
                          ? 'bg-indigo-500 border-indigo-500 text-white'
                          : 'border-gray-300 text-gray-700 hover:bg-indigo-50'
                      }`}
                    >
                      First Name
                    </button>
                    <button
                      onClick={() => setSearchField('lastName')}
                      class={`flex-1 px-4 py-2 rounded-lg border transition-colors ${
                        searchField === 'lastName'
                          ? 'bg-indigo-500 border-indigo-500 text-white'
                          : 'border-gray-300 text-gray-700 hover:bg-indigo-50'
                      }`}
                    >
                      Last Name
                    </button>
                  </div>
                </div>

                <div class="space-y-2">
                  <label class="block text-sm font-medium text-gray-700">Last Contacted</label>
                  <div class="grid grid-cols-2 sm:grid-cols-4 lg:grid-cols-2 xl:grid-cols-4 gap-2">
                    {[
                      { value: 'all', label: 'All Time' },
                      { value: 'week', label: '1 Week' },
                      { value: 'month', label: '1 Month' },
                      { value: 'threeMonths', label: '3 Months' }
                    ].map(option => (
                      <button
                        key={option.value}
                        onClick={() => setTimeFilter(option.value)}
                        class={`w-full px-3 py-2 rounded-lg border text-sm transition-colors ${
                          timeFilter === option.value
                            ? 'bg-indigo-500 border-indigo-500 text-white'
                            : 'border-gray-300 text-gray-700 hover:bg-indigo-50'
                        }`}
                      >
                        {option.label}
                      </button>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Add Person Modal */}
      {showAddModal && (
        <div class="fixed inset-0 bg-black/50 backdrop-blur-sm z-50 flex items-center justify-center">
          <div class="bg-white rounded-xl shadow-xl max-w-md w-full mx-4 overflow-hidden">
            <div class="p-6 space-y-6">
              <div class="flex items-center justify-between">
                <h2 class="text-xl font-semibold text-gray-800">Add New Person</h2>
                <button
                  onClick={() => setShowAddModal(false)}
                  class="text-gray-400 hover:text-gray-500 transition-colors"
                >
                  <svg class="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" />
                  </svg>
                </button>
              </div>

              {error && (
                <div class="bg-red-50 border border-red-200 text-red-600 rounded-lg p-3 text-sm">
                  {error}
                </div>
              )}

              <form onSubmit={handleAddPerson} class="space-y-4">
                <div>
                  <label class="block text-sm font-medium text-gray-700 mb-1">
                    First Name
                    <span class="text-red-500 ml-1">*</span>
                  </label>
                  <input
                    type="text"
                    name="firstName"
                    value={newPerson.firstName}
                    onInput={handleInputChange}
                    required
                    class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                    placeholder="Enter first name"
                  />
                </div>

                <div>
                  <label class="block text-sm font-medium text-gray-700 mb-1">
                    Last Name
                    <span class="text-red-500 ml-1">*</span>
                  </label>
                  <input
                    type="text"
                    name="lastName"
                    value={newPerson.lastName}
                    onInput={handleInputChange}
                    required
                    class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                    placeholder="Enter last name"
                  />
                </div>

                <div>
                  <label class="block text-sm font-medium text-gray-700 mb-1">
                    Phone Number
                    <span class="text-red-500 ml-1">*</span>
                  </label>
                  <input
                    type="tel"
                    name="phoneNumber"
                    value={newPerson.phoneNumber}
                    onInput={handleInputChange}
                    required
                    class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                    placeholder="Enter phone number"
                  />
                </div>

                <div class="flex items-center gap-2">
                  <label class="relative inline-flex items-center cursor-pointer">
                    <input
                      type="checkbox"
                      checked={showAddressField}
                      onChange={(e) => setShowAddressField(e.target.checked)}
                      class="sr-only peer"
                    />
                    <div class="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-indigo-300 rounded-full peer peer-checked:after:translate-x-full rtl:peer-checked:after:-translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:start-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-indigo-500"></div>
                    <span class="ms-3 text-sm font-medium text-gray-700">Add Address</span>
                  </label>
                </div>

                {showAddressField && (
                  <div class="animate-fadeIn space-y-4">
                    <div>
                      <label class="block text-sm font-medium text-gray-700 mb-1">Street Address</label>
                      <input
                        type="text"
                        name="address.street"
                        value={newPerson.address.street}
                        onInput={(e) => setNewPerson(prev => ({
                          ...prev,
                          address: { ...prev.address, street: e.target.value }
                        }))}
                        class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                        placeholder="Enter street address"
                      />
                    </div>
                    <div class="grid grid-cols-2 gap-4">
                      <div>
                        <label class="block text-sm font-medium text-gray-700 mb-1">City</label>
                        <input
                          type="text"
                          name="address.city"
                          value={newPerson.address.city}
                          onInput={(e) => setNewPerson(prev => ({
                            ...prev,
                            address: { ...prev.address, city: e.target.value }
                          }))}
                          class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                          placeholder="Enter city"
                        />
                      </div>
                      <div>
                        <label class="block text-sm font-medium text-gray-700 mb-1">State</label>
                        <input
                          type="text"
                          name="address.state"
                          value={newPerson.address.state}
                          onInput={(e) => setNewPerson(prev => ({
                            ...prev,
                            address: { ...prev.address, state: e.target.value }
                          }))}
                          class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                          placeholder="Enter state"
                          maxLength="2"
                        />
                      </div>
                    </div>
                    <div>
                      <label class="block text-sm font-medium text-gray-700 mb-1">ZIP Code</label>
                      <input
                        type="text"
                        name="address.zipCode"
                        value={newPerson.address.zipCode}
                        onInput={(e) => {
                          const value = e.target.value.replace(/\D/g, '').slice(0, 5);
                          setNewPerson(prev => ({
                            ...prev,
                            address: { ...prev.address, zipCode: value }
                          }));
                        }}
                        class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                        placeholder="Enter ZIP code"
                        maxLength="5"
                      />
                    </div>
                  </div>
                )}

                <div class="flex justify-end gap-3 pt-4">
                  <button
                    type="button"
                    onClick={() => setShowAddModal(false)}
                    class="px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors"
                  >
                    Cancel
                  </button>
                  <button
                    type="submit"
                    disabled={isSubmitting}
                    class={`px-4 py-2 rounded-lg text-white transition-all ${
                      isSubmitting
                        ? 'bg-indigo-400 cursor-not-allowed'
                        : 'bg-indigo-500 hover:bg-indigo-600 hover:shadow-lg hover:-translate-y-0.5'
                    }`}
                  >
                    {isSubmitting ? (
                      <div class="flex items-center gap-2">
                        <div class="animate-spin rounded-full h-4 w-4 border-2 border-white border-t-transparent"></div>
                        <span>Adding...</span>
                      </div>
                    ) : (
                      'Add Person'
                    )}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}

      {/* Loading State */}
      {isLoading ? (
        <div class="flex items-center justify-center py-12">
          <div class="animate-spin rounded-full h-12 w-12 border-b-2 border-indigo-500"></div>
        </div>
      ) : (
        <>
          {/* People Cards */}
          <div class="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 sm:gap-6">
            {filteredPeople.map(person => (
              <div
                key={person.id}
                onClick={() => route(`/person/${person.id}`)}
                class="transform transition-all duration-200 hover:-translate-y-1 hover:shadow-xl cursor-pointer rounded-xl border border-gray-200/50 shadow-md overflow-hidden"
                style={{
                  backgroundColor: getCardColor(person.lastContactedDate)
                }}
              >
                <div class="p-4 sm:p-6">
                  <h3 class="text-xl font-semibold text-white mb-2">
                    {person.firstName} {person.lastName}
                  </h3>
                  <p class="text-white/90 text-sm">
                    Last contacted: {formatDistanceToNow(new Date(person.lastContactedDate), { addSuffix: true })}
                  </p>
                  <p class="text-white/80 text-sm mt-2">
                    {person.phoneNumber}
                  </p>
                  {person.address && (
                    <p class="text-white/80 text-sm mt-2 line-clamp-1">
                      📍 {person.address.street}, {person.address.city}, {person.address.state} {person.address.zipCode}
                    </p>
                  )}
                  <div class="mt-3 text-white/70 text-sm">
                    {person.meetingNotes.length} meeting note{person.meetingNotes.length !== 1 ? 's' : ''}
                  </div>
                </div>
              </div>
            ))}
          </div>

          {filteredPeople.length === 0 && !isLoading && (
            <div class="text-center py-8 bg-white rounded-xl border border-gray-200 shadow-sm">
              <p class="text-gray-500 text-lg">No people found matching your criteria</p>
            </div>
          )}
        </>
      )}
    </div>
  );
};

export default Home; 