import { Router, route } from 'preact-router';
import { AuthProvider, useAuth } from './contexts/AuthContext';
import './assets/styles/app.css';

// Pages
import Home from './pages/Home';
import PersonDetail from './pages/PersonDetail';
import Login from './pages/Login';

function PrivateRoute({ component: Component, ...props }) {
  const { user, loading } = useAuth();
  console.log('PrivateRoute:', { user, loading }); // Debug log

  if (loading) {
    return (
      <div class="min-h-screen flex items-center justify-center">
        <div class="animate-spin rounded-full h-8 w-8 border-b-2 border-indigo-500"></div>
      </div>
    );
  }

  if (!user) {
    console.log('No user found, redirecting to login'); // Debug log
    return <Login />;
  }

  return <Component {...props} />;
}

function Navigation() {
  const { user, logout } = useAuth();
  console.log('Navigation:', { user }); // Debug log

  return user ? (
    <nav class="bg-gradient-to-r from-indigo-500/95 to-purple-500/95 shadow-md sticky top-0 z-50 backdrop-blur-md">
      <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div class="flex justify-between h-16">
          <div class="flex items-center">
            <h1 class="text-xl font-semibold text-white">Person Tracker</h1>
          </div>
          <div class="flex items-center space-x-4">
            <span class="text-white/90">Welcome, {user.username}</span>
            <button
              onClick={logout}
              class="text-white/90 hover:text-white px-4 py-2 rounded-lg hover:bg-white/10 transition-colors text-sm font-medium"
            >
              Logout
            </button>
          </div>
        </div>
      </div>
    </nav>
  ) : null;
}

function AppContent() {
  const { loading } = useAuth();
  console.log('AppContent:', { loading }); // Debug log

  if (loading) {
    return (
      <div class="min-h-screen flex items-center justify-center">
        <div class="animate-spin rounded-full h-8 w-8 border-b-2 border-indigo-500"></div>
      </div>
    );
  }

  return (
    <div class="min-h-screen bg-gradient-to-br from-indigo-50 via-white to-purple-50">
      <Navigation />
      <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        <Router>
          <Login path="/login" />
          <PrivateRoute path="/" component={Home} />
          <PrivateRoute path="/person/:id" component={PersonDetail} />
        </Router>
      </div>
    </div>
  );
}

export default function App() {
  console.log('App component rendering'); // Debug log
  return (
    <AuthProvider>
      <AppContent />
    </AuthProvider>
  );
}
