import { useState, useEffect, useRef } from 'preact/hooks';
import { route } from 'preact-router';
import axios from 'axios';
import { format } from 'date-fns';
import { useAuth } from '../contexts/AuthContext';
import { getApiUrl } from '../config';

const PersonDetail = ({ matches }) => {
  const [person, setPerson] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [newNote, setNewNote] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [attendees, setAttendees] = useState(['']);
  const { user } = useAuth();
  const latestNoteRef = useRef(null);

  useEffect(() => {
    if (!user) {
      route('/login');
      return;
    }

    const personId = matches?.id;
    if (!personId) {
      console.error('No ID provided to PersonDetail');
      setError('No ID provided');
      setLoading(false);
      return;
    }

    fetchPersonDetails(personId);
  }, [matches?.id, user]);

  const fetchPersonDetails = async (personId) => {
    setLoading(true);
    setError(null);
    try {
      console.log('Fetching details for person:', personId);
      const response = await axios.get(getApiUrl(`/people/${personId}`));
      console.log('Person details response:', response.data);
      
      if (!response.data) {
        throw new Error('No data received from server');
      }
      
      setPerson(response.data);
    } catch (error) {
      console.error('Error fetching person details:', error);
      if (error.response?.status === 401) {
        route('/login');
      } else {
        setError(error.response?.data?.detail || 'Failed to load person details');
        setPerson(null);
      }
    } finally {
      setLoading(false);
    }
  };

  const handleAddNote = async (e) => {
    e.preventDefault();
    if (!newNote.trim()) {
      return;
    }
    
    setIsSubmitting(true);
    try {
      const response = await axios.post(getApiUrl(`/people/${personId}/notes`), {
        content: newNote,
        date: new Date().toISOString()
      });
      
      // Update person state with the response data
      setPerson(response.data);
      setNewNote('');
      setAttendees(['']);
      setError(null);

      // Scroll to the latest note after a short delay to ensure the DOM has updated
      setTimeout(() => {
        if (latestNoteRef.current) {
          latestNoteRef.current.scrollIntoView({ 
            behavior: 'smooth',
            block: 'center'
          });
        }
      }, 100);
    } catch (error) {
      console.error('Error adding note:', error);
      setError(error.response?.data?.detail || 'Failed to add note');
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleAttendeeChange = (index, value) => {
    const newAttendees = [...attendees];
    newAttendees[index] = value;
    setAttendees(newAttendees);
  };

  const addAttendeeField = () => {
    setAttendees([...attendees, '']);
  };

  const removeAttendeeField = (index) => {
    const newAttendees = attendees.filter((_, i) => i !== index);
    setAttendees(newAttendees);
  };

  if (loading) {
    return (
      <div class="flex items-center justify-center min-h-[50vh]">
        <div class="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div class="bg-white rounded-xl border border-gray-200 shadow-sm p-8 text-center">
        <p class="text-red-600 text-lg mb-4">{error}</p>
        <button
          onClick={() => route('/')}
          class="px-6 py-2 bg-indigo-500 text-white rounded-lg hover:bg-indigo-600 transition-colors"
        >
          Go Back
        </button>
      </div>
    );
  }

  if (!person) {
    return (
      <div class="bg-white rounded-xl border border-gray-200 shadow-sm p-8 text-center">
        <p class="text-gray-600 text-lg mb-4">Person not found</p>
        <button
          onClick={() => route('/')}
          class="px-6 py-2 bg-indigo-500 text-white rounded-lg hover:bg-indigo-600 transition-colors"
        >
          Go Back
        </button>
      </div>
    );
  }

  return (
    <div class="space-y-6">
      <div class="sticky top-[4.5rem] z-10">
        <div class="bg-gradient-to-r from-indigo-500/95 to-purple-500/95 backdrop-blur-md rounded-xl border border-indigo-200 shadow-sm p-4">
          <button
            onClick={() => route('/')}
            class="group px-4 py-2 text-white hover:text-white/90 flex items-center gap-2 transition-colors"
          >
            <svg class="w-5 h-5 transform transition-transform group-hover:-translate-x-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M10 19l-7-7m0 0l7-7m-7 7h18" />
            </svg>
            Back to List
          </button>
        </div>
      </div>

      <div class="bg-gradient-to-b from-white to-indigo-50/30 rounded-xl border border-gray-200 shadow-sm overflow-hidden">
        <div class="p-6 sm:p-8">
          <h2 class="text-2xl font-bold text-gray-800 mb-6 flex items-center gap-3">
            <span>Person Details</span>
            <span class="h-px flex-1 bg-indigo-200/50"></span>
          </h2>
          
          <div class="grid grid-cols-1 sm:grid-cols-2 gap-6 mb-8">
            {[
              { label: 'First Name', value: person.firstName },
              { label: 'Last Name', value: person.lastName },
              { label: 'Phone Number', value: person.phoneNumber },
              { 
                label: 'Last Contacted',
                value: format(new Date(person.lastContactedDate), 'PPP')
              }
            ].map(field => (
              <div class="space-y-2 bg-gradient-to-br from-white to-indigo-50/50 rounded-lg p-4 border border-indigo-100/50 hover:shadow-md transition-all">
                <label class="text-sm text-indigo-600/70">{field.label}</label>
                <p class="text-lg font-medium text-gray-800">{field.value}</p>
              </div>
            ))}
            {person.address && (
              <div class="sm:col-span-2 space-y-2 bg-gradient-to-br from-white to-indigo-50/50 rounded-lg p-4 border border-indigo-100/50 hover:shadow-md transition-all">
                <label class="text-sm text-indigo-600/70">Address</label>
                <div class="grid grid-cols-1 gap-2">
                  <p class="text-lg font-medium text-gray-800">{person.address.street}</p>
                  <p class="text-lg font-medium text-gray-800">
                    {person.address.city}, {person.address.state} {person.address.zipCode}
                  </p>
                </div>
              </div>
            )}
          </div>

          <div class="border-t border-indigo-100 pt-6">
            <div class="flex items-center gap-3 mb-6">
              <h3 class="text-xl font-semibold text-gray-800">Meeting Notes</h3>
              <span class="h-px flex-1 bg-indigo-200/50"></span>
              <span class="px-3 py-1 bg-gradient-to-r from-indigo-500 to-purple-500 text-white text-sm rounded-full font-medium shadow-sm">
                {person.meetingNotes.length} notes
              </span>
            </div>

            {/* Add New Note Form */}
            <form onSubmit={handleAddNote} class="mb-6 space-y-4">
              {error && (
                <div class="bg-red-50 border border-red-100 text-red-600 p-3 rounded-lg">
                  {error}
                </div>
              )}
              
              <div class="space-y-4">
                <div>
                  <label class="block text-sm font-medium text-gray-700 mb-1">Meeting Note</label>
                  <textarea
                    value={newNote}
                    onInput={(e) => setNewNote(e.target.value)}
                    placeholder="Add a new meeting note..."
                    class="w-full px-4 py-3 rounded-lg border border-indigo-200 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 resize-none bg-white text-gray-700 placeholder-gray-400"
                    rows="3"
                    required
                  />
                </div>
                
                <div class="space-y-3">
                  <div class="flex items-center justify-between">
                    <div class="flex items-center gap-2">
                      <label class="block text-sm font-medium text-gray-700">Meeting Attendees</label>
                      <span class="text-xs text-red-500">*</span>
                    </div>
                    <button
                      type="button"
                      onClick={addAttendeeField}
                      class="text-sm text-indigo-600 hover:text-indigo-700 flex items-center gap-1"
                    >
                      <svg class="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
                      </svg>
                      Add Attendee
                    </button>
                  </div>
                  
                  <div class="space-y-2">
                    {attendees.map((attendee, index) => (
                      <div key={index} class="flex gap-2">
                        <input
                          type="text"
                          value={attendee}
                          onInput={(e) => handleAttendeeChange(index, e.target.value)}
                          placeholder={index === 0 ? "Enter attendee name (required)" : "Enter attendee name"}
                          class="flex-1 px-4 py-2 rounded-lg border border-indigo-200 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 bg-white text-gray-700 placeholder-gray-400"
                          required={index === 0}
                        />
                        {index > 0 && (
                          <button
                            type="button"
                            onClick={() => removeAttendeeField(index)}
                            class="p-2 text-red-500 hover:text-red-600 hover:bg-red-50 rounded-lg transition-colors"
                          >
                            <svg class="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                            </svg>
                          </button>
                        )}
                      </div>
                    ))}
                  </div>
                </div>
              </div>

              <div class="flex justify-end">
                <button
                  type="submit"
                  disabled={isSubmitting || !newNote.trim() || !attendees.some(a => a.trim())}
                  class={`px-6 py-3 rounded-lg font-medium transition-all ${
                    isSubmitting || !newNote.trim() || !attendees.some(a => a.trim())
                      ? 'bg-gray-100 text-gray-400 cursor-not-allowed'
                      : 'bg-gradient-to-r from-indigo-500 to-purple-500 text-white hover:shadow-lg hover:-translate-y-0.5'
                  }`}
                >
                  {isSubmitting ? (
                    <div class="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
                  ) : (
                    'Add Note'
                  )}
                </button>
              </div>
            </form>

            <div class="space-y-4 max-h-[500px] overflow-y-auto pr-2 custom-scrollbar">
              {person.meetingNotes && person.meetingNotes.length > 0 ? (
                [...person.meetingNotes]
                  .sort((a, b) => new Date(b.date) - new Date(a.date))
                  .map((note, index) => (
                    <div 
                      key={index}
                      ref={index === 0 ? latestNoteRef : null}
                      class={`rounded-lg p-5 border shadow-sm hover:shadow-md transition-all ${
                        index === 0 
                          ? 'bg-gradient-to-br from-indigo-50 to-purple-50 border-indigo-200/50' 
                          : 'bg-gradient-to-br from-white to-gray-50 border-gray-200/50'
                      }`}
                    >
                      <div class="flex items-center gap-3 mb-3">
                        <div class="flex items-center gap-2 bg-white px-3 py-1 rounded-full border border-indigo-100 shadow-sm">
                          <svg class="w-4 h-4 text-indigo-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" />
                          </svg>
                          <p class="text-sm text-gray-600 font-medium">
                            {format(new Date(note.date), 'PPP')}
                          </p>
                        </div>
                        {index === 0 && (
                          <span class="text-xs text-white font-medium bg-gradient-to-r from-indigo-500 to-purple-500 px-2 py-1 rounded-full shadow-sm">
                            Latest
                          </span>
                        )}
                      </div>
                      
                      <p class="text-gray-700 whitespace-pre-wrap leading-relaxed mb-4">
                        {note.text}
                      </p>

                      {note.attendees && note.attendees.length > 0 ? (
                        <div class="mt-4 bg-white rounded-lg border border-indigo-100 p-3">
                          <div class="flex items-center gap-2 mb-2">
                            <svg class="w-4 h-4 text-indigo-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z" />
                            </svg>
                            <span class="text-sm font-medium text-gray-700">Meeting Attendees</span>
                          </div>
                          <div class="flex flex-wrap gap-2">
                            {note.attendees.map((attendee, i) => (
                              <span
                                key={i}
                                class="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-indigo-50 text-indigo-700 border border-indigo-100"
                              >
                                {attendee}
                              </span>
                            ))}
                          </div>
                        </div>
                      ) : (
                        <div class="mt-4 text-sm text-gray-500 italic">No attendees recorded</div>
                      )}
                    </div>
                  ))
              ) : (
                <div class="text-center py-12 bg-gradient-to-br from-white to-indigo-50 rounded-lg border border-indigo-100">
                  <svg class="w-12 h-12 text-indigo-300 mx-auto mb-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                  </svg>
                  <p class="text-gray-500 text-lg">No meeting notes available</p>
                  <p class="text-gray-400 text-sm mt-1">Add your first note using the form above</p>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PersonDetail; 