// API URL configuration
export const API_URL = import.meta.env.VITE_API_URL || '/api';
window.console.log('API_URL:');
window.console.log('process.env:', process.env);
window.console.log('import.meta.env:', import.meta.env);
window.console.log('VITE_API_URL:', import.meta.env.VITE_API_URL);
window.console.log('API_URL:', API_URL);

// Use this for API calls
export const getApiUrl = (endpoint) => {

  const path = endpoint.startsWith('/') ? endpoint : `/${endpoint}`;
  
  // Log the final API URL for debugging
  const finalUrl = API_URL.startsWith('https') ? 
    (API_URL.endsWith('/api') ? `${API_URL}${path}` : `${API_URL}/api${path}`) : 
    `${API_URL}${path}`;
    
  console.log('API request to:', finalUrl);
  return finalUrl;
}; 