import { createContext } from 'preact';
import { useState, useEffect, useContext } from 'preact/hooks';
import axios from 'axios';
import { getApiUrl } from '../config';

const AuthContext = createContext();

export function useAuth() {
  return useContext(AuthContext);
}

export function AuthProvider({ children }) {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const initAuth = async () => {
      console.log('Initializing auth...'); // Debug log
      const token = localStorage.getItem('token');
      const storedUser = localStorage.getItem('user');
      
      console.log('Stored auth data:', { token: !!token, storedUser: !!storedUser }); // Debug log
      
      if (token && storedUser) {
        try {
          // Set the default authorization header
          axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
          
          // Verify the token is still valid by making a test request
          await axios.get(getApiUrl('/people'));
          
          setUser(JSON.parse(storedUser));
          console.log('Auth initialized with user:', JSON.parse(storedUser)); // Debug log
        } catch (error) {
          console.error('Token validation failed:', error);
          // If token is invalid, clear storage
          localStorage.removeItem('token');
          localStorage.removeItem('user');
          delete axios.defaults.headers.common['Authorization'];
        }
      } else {
        console.log('No stored auth data found'); // Debug log
      }
      
      setLoading(false);
    };

    initAuth();
  }, []);

  const validateToken = async (token) => {
    try {
      localStorage.setItem('auth_token', token);
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
      
      // Test the token by making a request
      await axios.get(getApiUrl('/people'));
      
      // ... rest of the function
    } catch (error) {
      // ... error handling
    }
  };

  const login = async (username, password) => {
    try {
      setLoading(true);
      setError(null);
      
      const response = await axios.post(getApiUrl('/login'), {
        username,
        password
      });
      
      // ... rest of the function
    } catch (error) {
      console.error('Login failed:', error);
      return false;
    }
  };

  const logout = () => {
    console.log('Logging out...'); // Debug log
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    delete axios.defaults.headers.common['Authorization'];
    setUser(null);
  };

  const value = {
    user,
    login,
    logout,
    loading
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
} 