import { useState } from 'preact/hooks';
import { route } from 'preact-router';
import { useAuth } from '../contexts/AuthContext';

export default function Login() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const { login } = useAuth();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setIsLoading(true);

    try {
      const success = await login(username, password);
      if (success) {
        route('/');
      } else {
        setError('Invalid username or password');
      }
    } catch (err) {
      setError('An error occurred during login');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div class="min-h-screen flex items-center justify-center bg-gradient-to-br from-indigo-50 via-white to-purple-50">
      <div class="w-full max-w-md transform transition-all">
        <div class="bg-white rounded-xl border border-indigo-100 shadow-lg overflow-hidden">
          <div class="bg-gradient-to-r from-indigo-500 to-purple-500 px-6 py-8">
            <h2 class="text-2xl font-bold text-center text-white">
              Welcome to Person Tracker
            </h2>
          </div>
          
          <div class="p-6">
            {error && (
              <div class="bg-red-50 border border-red-100 text-red-600 p-3 rounded-lg mb-4">
                {error}
              </div>
            )}

            <form onSubmit={handleSubmit} class="space-y-4">
              <div>
                <label htmlFor="username" class="block text-sm font-medium text-gray-700 mb-1">
                  Username
                </label>
                <input
                  id="username"
                  type="text"
                  value={username}
                  onInput={(e) => setUsername(e.target.value)}
                  class="w-full px-4 py-2 rounded-lg border border-indigo-200 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 bg-white text-gray-900"
                  required
                />
              </div>

              <div>
                <label htmlFor="password" class="block text-sm font-medium text-gray-700 mb-1">
                  Password
                </label>
                <input
                  id="password"
                  type="password"
                  value={password}
                  onInput={(e) => setPassword(e.target.value)}
                  class="w-full px-4 py-2 rounded-lg border border-indigo-200 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 bg-white text-gray-900"
                  required
                />
              </div>

              <button
                type="submit"
                disabled={isLoading}
                class={`w-full py-2 px-4 rounded-lg font-medium transition-all ${
                  isLoading
                    ? 'bg-gray-100 text-gray-400 cursor-not-allowed'
                    : 'bg-gradient-to-r from-indigo-500 to-purple-500 text-white hover:shadow-lg hover:-translate-y-0.5'
                }`}
              >
                {isLoading ? (
                  <div class="flex items-center justify-center">
                    <div class="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
                  </div>
                ) : (
                  'Sign in'
                )}
              </button>

              <p class="text-center text-sm text-gray-500 mt-4">
                Use username: <span class="font-medium text-indigo-600">admin</span> and password: <span class="font-medium text-indigo-600">admin123</span>
              </p>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
} 